.row.grey{
 background-color: @gray-row;

}
.row.grey-light{
  background-color: @gray-row-light;

}

.big-panel{
  display: block;
  text-align: center;
  padding: 40px 0;

  img{
    margin:auto;
  }
  &:hover{
    img{
      /* IE 8 */
      -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=80)";
      /* IE 5-7 */
      filter: alpha(opacity=80);
      /* Netscape */
      -moz-opacity: 0.8;
      /* Safari 1.x */
      -khtml-opacity: 0.8;
      /* Good browsers */
      opacity: 0.8;
    }

  }
  h3{
    text-transform: uppercase;
    text-align: left;
    margin-top: 5px;
    font-family: @font-family-thin;
    small{
      text-transform: none;
      display: block;
      color:@gray-dark;
    }
  }
}

.hero-title{
  font-family: @font-family-thin;
  text-transform: uppercase;
  text-align: center;
  margin:80px 0;
}

.release{
  @media (max-width: @screen-xs-max){
    h3 {
      min-height: 55px;
    }
  }
  a{
    border:2px solid transparent;
    display: block;
    overflow: hidden;
    -webkit-transition: all 100ms ease-in-out;
    -moz-transition: all 100ms ease-in-out;
    -ms-transition: all 100ms ease-in-out;
    -o-transition: all 100ms ease-in-out;
    transition: all 100ms ease-in-out;
    &:hover{
      border-color: @brand-primary;

    }
  }
  @media(max-width: @screen-xs-max){
    padding-bottom: 10px;
  }
  padding-bottom: 30px;
  .img-responsive{
    width: 100%;
  }
  .title{
    font-size: @font-size-h4;
  }
}

h3{
    &.vertical-align{
        font-size: @font-size-h3;
        line-height: (@font-size-h3*2);
        height: (@font-size-h3*2);
        .inner{
            font-size:@font-size-h3;
            line-height: @font-size-h3;
            display:inline-block;
            vertical-align: middle;
        }
        @media(max-width: @screen-xs-min){
            font-size: (@font-size-h3*0.65);
            line-height: ((@font-size-h3*2)*0.65);
            height: ((@font-size-h3*2)*0.65);
            .inner{
                font-size: (@font-size-h3*0.65);
                line-height: (@font-size-h3*0.65);
            }
        }
    }
}
