.html5-video-player{
  .html5-main-video{
    z-index: 1;
  }
  .html5-video-content{
    position: absolute;
    top: 0;
    right: 0;
    width: 100%;
    height: 70%;
    .popup-container{
      position: absolute;
      top: 2%;
      right: 2%;
      width: 260px;
    }
  }
}
body.video-fullscreen{
  .product-notification{
    top: 0;
    z-index: 2147483647;
  }
}

.product-notification{
  position: fixed;
  top: @navbar-height + 10px;
  right: 0;
  z-index: 2147483647;
  display: none;

  .panel-close{
    position: absolute;
    top:5px;
    right: 7px;
  }
  .product-promotion-fragment-description{
    font-size: 1.2em;
    margin-top:10px;
    .label{
      display: inline-block;

    }
  }
  .panel-footer{
    padding: 0;
    &.block-buttons{
      .row,.col-xs-6{
        padding: 0;
        margin:0;
      }
    }
    .order-response{
      padding: 10px;
      color: @brand-dark;

    }
  }
}

