.main-navbar{
  border-radius: 0;
  .navbar-brand{
    padding-top: 21px;
    padding-bottom: 16px;
    > a > img {
    }
    @media(min-width: @screen-xs-min){
      padding-right: 20px;
    }
    @media(min-width: @screen-sm-min){
      padding-right: 15px;
      padding-left: 15px;
    }
    @media(min-width: @screen-md-min){
      padding-right: 30px;
      font-size: 36px;
      margin-left: 55px;
    }

  }
  .navbar-nav {
    > li > a {
      padding-bottom: 10px;
      font-family: @font-family-thin;
      text-transform: uppercase;
      @media(max-width: @screen-sm-max){
          padding-left:8px;
          padding-right:8px;
      }
      &:hover,
      &:focus {
        @media(min-width:@screen-sm-min ){
          border-bottom: 5px solid;
        }
      }
    }
    > .active > a {
      &,
      &:hover,
      &:focus {
        @media(min-width:@screen-sm-min ){
          border-bottom: 5px solid;
        }
        font-weight:bold;
        font-family: @font-family-base;
        background-color:transparent;
      }
    }
    .user-login{
      a{
        border-right: 1px solid;
      }
    }
  }
  .navbar-toggle{
    border: none;
    margin-top: 15px ;
    &:focus,&:hover{
      background-color: transparent;
    }
    &.collapsed{
      margin-top: 23px;
      .icon-bar{
        display: block;
      }
      .icon-x{
        display: none;
      }
    }
    .icon-bar{
      display: none;
    }
    .icon-x{
      display: block;
      color: #fff;
    }
  }
}


.breadcrumb{
  ul{
    padding: 0;
    margin: 0;
    li{
