}

li.miniCart{
  position: relative;
  .miniCartPopup{
    display: none;
    position: absolute;
    background-color: #18bc9c;
    width: calc(100% + 15px);
    padding: 0 10px 15px 10px;
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;
    right: -15px;
    margin-top: -2px;
    border-top: 1px solid;
    @media (max-width: @screen-xs-max) {
      display: none !important;
    }
    .empty-popup-cart{
      font-size: @font-size-h4;
      color: @body-bg;
    }
    .total{
      margin: 1vh 0;
      border-top: 1px solid;
      font-weight: bold;
    }
  }
}





