/** CECS-369 add new payment on my-account **/

@error-red: #b94a48;
@error-red-dark: #b94a48;
@error-red-light: #d59392;
@error-red-washed: #f2dede;

.control-group.error
{
    .control-label,.help-block,.help-inline,.checkbox,.radio,input,select,textarea
    {
	    color: @error-red;
    }
    input,select,textarea
    {
        border-color: @error-red;
        box-shadow:inset 0 1px 1px rgba(0, 0, 0, 0.075);
    }
    input:focus,select:focus,textarea:focus
    {
        border-color: @error-red-dark;
        box-shadow:inset 0 1px 1px rgba(0, 0, 0, 0.075),0 0 6px @error-red-light;
    }
    .input-prepend .add-on,.control-group.error .input-append .add-on
    {
        color: @error-red;
        background-color: @error-red-washed;
        border-color: @error-red;
    }
}

