.page-productDetails{

  .gallery{
    .main-photo{
      margin:0 auto;
      border:1px solid @brand-primary;
      width: 100%;
    }
    .thumbnails{
      padding: 0;
      margin: 10px 0 0 0;
      list-style: none;
      @media(max-width: @screen-xs-max){
        text-align: center;
      }
      li{
        display: inline-block;
        margin: 0 5px 5px 0;
        border: 1px solid @brand-dark;
        -webkit-transition: border 100ms;
        -moz-transition: border 100ms;
        -ms-transition: border 100ms;
        -o-transition: border 100ms;
        transition: border 100ms;
        &:first-child{
          margin-left: 0;
        }
        a{
          display: block;
        }
        &:hover{
          border-bottom: 4px solid @brand-primary;
        }
        &.active{
          border-bottom: 4px solid @brand-primary;
        }
      }
    }
  }

  .product-name{
    color: @brand-dark;
    font-family: @font-family-bold;
    text-transform: uppercase;
    @media (min-width: @screen-sm-min){
      margin-top: -9px;
    }
  }
    .price{
        @media(max-width: @screen-sm-max){
            font-size: @font-size-h2;
        }
        font-size: @font-size-h3;
        margin-bottom: 10px;
        display: block;
    }
  .rating {
    direction: rtl;
    display: inline-block;
    margin-bottom: 2vh;
    font-size: 1.5em;
    vertical-align: text-top;
    &>span,&>label>span {
      display: inline-block;
      position: relative;
      color:#ffffff;
      text-align: left;
      width: 20px;
      &:before{
        content:"\f069";
      }
      &.active{
        color:@brand-primary;
      }
    }
  }
  .rating-count{
    padding-left: 10px;
    display: inline-block;
    color: @brand-primary;
  }
  .variants{
    .options{
      list-style: none;
      padding: 0;
      margin: 10px 0 0 0;
      li{
        display: inline-block;
        border:1px solid @gray-light;
        a{
          display: block;
          min-width: 40px;
          min-height: 40px;
        }
        &:hover{
          border-color: @brand-primary;

        }
      }
    }
  }
}
.buy{
  margin-top: 10px;
  .input-blue{
    color: #fff;
    background-color: @brand-primary;
    border-color: @brand-primary;
  }
}

.description{
  margin-top: 10px;
  padding-top: 10px;
  border-top: 1px dashed @brand-dark;
  margin-bottom: 10px;
}

.reviews{
  display: table;
  width: 100%;
  margin-top: 20px ;
  .review{
    display: inline-table;
    padding-right: 20px;
    width: 33%;
    @media (max-width: @screen-xs-max){
      width: 100%;
      border-bottom: 1px dashed @brand-dark;
      display: block;
      padding-bottom: 10px;
      padding-right: 0;
    }
    .title{
      font-size: 1.5em;
      font-family: @font-family-bold;
    }
    .text{
      color: @gray-light;
      padding-bottom: 10px;
    }
  }
}

.product-features{
  dt{
    color: @brand-orange;

  }

}

.stars-container{
  text-align: left;
  width: 125px;
  display: inline-block;
  vertical-align: bottom;
  .stars-size-control{
    overflow: hidden;
    .rating{
      display: block;
      white-space: nowrap;
      direction: ltr;
      margin-bottom: 0;
    }
  }
}
#based_on_reviews{
  display: inline-block;

}

.input-rating{
  display: inline-block;
  vertical-align: middle;
  input.rating { display: none; }

  label{
    &.rating{
      float: right;
      font-size: 1.5em;
      width: 25px;
      cursor: pointer;
      margin-bottom: 0;
      &:before{
        content: '\f069';
        color: @gray-light;
        display: inline-block;
        font: normal normal normal 14px/1 FontAwesome;
        font-size: inherit;
        text-rendering: auto;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
        transform: translate(0, 0);
      }
    }
  }

  input.rating:checked ~ label.rating:before {
    color: @brand-primary;
  }

  label.rating:hover ~ label.rating:before,label.rating:hover:before{
    color:darken(@brand-primary,20%)!important;
  }

}

.instantcheckout-container,.prod_add_to_prodlist {
    display: inline-block;
    margin-right: 5px;
}

// Variant selector

@variantSelector-select:   	 {width: 40px; height: 40px; border: 1px @gray-light solid; margin-right: 10px;};
@variantSelector-hoverBorder:{border-bottom: 4px solid @brand-primary;};

#colorVariant{
  display: flex;
	.colorVariantSelect{
	  @variantSelector-select();
	}
	.colorVariantSelect:hover{
 		@variantSelector-hoverBorder();
	  }
}

#sizeVariant{
  display: flex;
	.sizeSelect{
	  @variantSelector-select();
	  text-align: center;
	  padding: 10px;	  
	  color: @gray-light;
	  background-color: #ffffff;
	  font-weight: bold;
	  float: left;
	  }
	  .sizeSelect:hover{
 		 color: @brand-primary;
 		 @variantSelector-hoverBorder();
	  }
}

#colorName, #sizeName{
  color: @gray-light;
  text-transform: none;
}
