.banners-container{
  padding: 20px 0;
  .banners-list{
    list-style: none;
    padding: 0;
    margin: 0;
    font-size: 0;
    .banner{
      position: relative;
      display: inline-flex;
      padding: 5px;
      img{
        width: 100%;
      }
      h3.title{
        small{
          display: block;
        }
      }
    }
  }
}

@media (min-width: @screen-sm-min){
  .banners-2{
    padding: 0;
    margin-top:@navbar-height;
    .banner{
      width: 50%;
        padding: 0;
    }
  }
  .banners-3{
    padding: 0;
    margin-top:@navbar-height;
     .banner{
       width: 33.3%;
         padding: 0;
     }
  }
  .banners-4{
    padding: 0;
    margin-top:@navbar-height;
    .banner{
        width: 25%;
        padding: 0;
    }
  }
}
@media (max-width: @screen-xs-max){
  .banner{
    width: 100%;
  }
  .banners-container {
    .banners-list .banner {
      display: block;
      width: 85%;
      margin: 0 auto 25px;
      &:last-child {
        margin-bottom: 0;
      }
    }
    &.round .banners-list .banner {
      width: 60%; 
    }
  }
}


.top-banner{
  padding: 0;
  margin-top: -@navbar-height;
  @media (max-width: @screen-xs-max){
    margin-top: @navbar-height;
  }
  &>.row{
    margin: 0;
    .banner-12{
      padding: 0;
    }
    .banners-container{
      .banner{
        padding: 0;
      }

    }
  }
  .img-responsive{
    width: 100%;
  }

  .banner-description{
    margin:0;
    position: absolute;
    bottom: 0;
    text-align: center;
    background-color:fadeout(@brand-secondary,55%) ;
    color:#fff;
    width: 100%;
    text-transform: uppercase;
    padding: 25px 0;
    font-size: 1.5em;
    -webkit-transition: 250ms ease;
    -moz-transition: 250ms ease;
    -ms-transition: 250ms ease;
    -o-transition: 250ms ease;
    transition: 250ms ease;
    @media (max-width: @screen-xs-max){
      position: relative;
      background-color:@brand-secondary ;
    }
    h3{
      margin: 0;
      text-transform: uppercase;
      text-align: left;
      padding-left: 25px;
      font-size: 2vw;
      span{
        display: block;
      }
      small{
        display: block;
        color: #ffffff;
        text-transform: none;
        font-family: @font-family-thin;
      }
    }
    &.description-center{
      top:40%;
      background-color: transparent;
      bottom: inherit;
      font-size: 4vw;
      @media (max-width: @screen-xs-max){
        position: absolute;
        top:25%;
      }
    }
  }
  a:hover{
    @media (min-width: @screen-sm-min){
      .banner-description{
        padding: 50px 0;
        text-decoration: none;
      }
    }
  }
  .banners-container{


  }
}
