.refinements-container{
  margin-bottom: 10px;
  margin-top: 50px;
  
  .show-filters{
   .fa{
     padding-top: 2px;
     &:before{
       content: "\f102";
     }
   }
  &.collapsed{
    .fa:before{
      content: "\f103";
    }
  }
  }
}
.refinements{
  &.collapse{
    @media(min-width: @screen-sm-min){
      display: block;
      visibility: visible;
    }
  }
  .panel-collapse{
    margin-bottom: 20px;
  }
  .panel,.panel-body{
    border:none;
    -webkit-box-shadow: none;
    -moz-box-shadow: none;
    box-shadow: none;
  }
  .panel-heading{
    background-color: @brand-dark;
    border-radius: 0;
    color:#fff;
    a{
      display: block;
      color:#fff;
      text-transform: uppercase;
      .fa{
        &:before{
          content: "\f102";
        }
      }
      &.collapsed{
        .fa:before{
          content: "\f103";
        }
      }
    }
  }
  .panel-body{
    background-color: #fafafa;
    .form-group{
      margin-bottom: 0;
      .checkbox{
        padding-top: 0;
      }
    }
  }
  .remove_item{
    padding-bottom: 5px;
    .remove_item_name{
      padding-right: 5px;
      
    }
    
  }
}