@cf-blue-light: #CDEAF5;
@cf-blue : #58B8DC;
@cf-blue-dark: #3498db;
@cf-blue-dark2: #2077b2;

#checkoutFunnel{
    margin:15px -15px;
    height:50px;

    @media(max-width: @screen-xs-max){
        margin-top:55px;
    }

    @media(max-width: @screen-xs-min){
        margin-top:-20px;
    }

    ul{
        list-style-type: none;
        padding:0;
        li {
            width: 25%;
            margin: 0;
            float:left;

            @media(max-width: @screen-xs-max){
                width:100%;
                clear:both;
            }

            &.step1 a{
                border-left-style: solid;
                border-left-width: 1px;
            }
            &.step4 a{
                border-right-style: solid;
                border-right-width: 1px;
                .arrow{
                    display:none;
                }
            }
        }

        li.current a{
            background:@cf-blue-dark;
            color:#fff;
            border-color: @cf-blue-dark2;
            .arrow:after{
                border-color:@cf-blue-dark2;
                background:@cf-blue-dark;
            }
        }
        
        li.disabledStep a{
        	pointer-events: none;
   			cursor: default;
        }

        //@media(max-width: @screen-xs-max){
        //    li.current a{
        //        &:before{
        //            border-color:@cf-blue-dark;
        //            background:@cf-blue-light;
        //        }
        //    }
        //}
    }

    a{
        color:rgb(25, 43, 54);
        display:block;
        height:50px;
        width:100%;
        padding:0;
        border-top:1px solid @cf-blue;
        border-bottom:1px solid @cf-blue;
        border-color:@cf-blue;
        background: @cf-blue-light;

        @media(max-width: @screen-xs-max){
            border:1px solid @cf-blue;
        }

        span, div{
            display:inline-block;
            float:left;
            height:50px;
        }
        & > span{
            font-size:40px;
            line-height: 50px;
            font-weight:bold;
            margin:0 15px 0 25%;
        }
        div{
            padding:5px 0;
            span{
                display:inline-block;
                float:left;
            }
            span:nth-child(1){
                padding-top:4px;
                height: 20px;
                line-height:20px;
                font-size:18px;
            }
            span:nth-child(2){
                padding-top:1px;
                height:15px;
                font-size:0.80em;
                text-transform: lowercase;
                clear:left;
            }
        }
        .arrow{
            float:right;
            width:58px;
            height:48px;
            margin-right:-25px;
            padding:0;
            overflow:hidden;
            &:after{
                content:'';
                display:block;
                background: @cf-blue-light;
                width:48px;
                height:48px;
                transform: rotate(45deg);
                border-top:1px solid @cf-blue;
                border-right:1px solid @cf-blue;
            }
        }

        @media(max-width: @screen-xs-max){
            overflow: hidden;
            .arrow{
                display:none;
            }
            & > span{
                margin-left:30px;
            }
            //&:before{
            //    content:'';
            //    display:block;
            //    float:left;
            //    height:48px;
            //    width:48px;
            //    //background:#fff;
            //    transform: rotate(45deg);
            //    margin-left:-33px;
            //    border-top:1px solid transparent;
            //    border-right:1px solid transparent;
            //    //margin-top:-25px;
            //}
        }

    }

    &:after{
        content:'';
        display:block;
        width:100%;
        clear:both;
    }
}

/* fix for different steps */

.page-guidedsellingPage{
    #checkoutFunnel{
        @media(max-width: @screen-xs-max){
            margin-top:0;
        }
        @media(max-width: @screen-xs-min){
            margin-top:-95px;
        }
    }
}
