.account{
    color:@gray-base;
    margin-top: 50px;
    .smaller-padding{
        margin-left: -10px;
        margin-right: -10px;
        margin-top:1vh;

    }
    .col-xs-6{
        padding-left: 10px;
        padding-right: 10px;
    }
    .social{
        margin-left: -7px;
        margin-top: 5px;
    }
    .dashed-bottom{
        border-bottom: 1px dashed;
        margin-bottom: 15px;
        padding-bottom: 10px;

        @media (min-width: @screen-sm-min){
            padding-left: 0;
        }
    }
    .customer-name{
        margin-top: 0;
        font-weight: bold;
    }

    .usage-charges {
        line-height: 2em;
    }
}

.account-tile{
    position: relative;
    width: 100%;
    overflow: hidden;
    background-color: @brand-primary;
    color: #fff;
    margin-bottom: 15px;
    &.tile-secondary{
        background-color: @brand-primary-light;
    }
    &:before{
        content: "";
        display: block;
        padding-top: 100%;
    }
    .account-content{
        position:  absolute;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        color: white;
        border: 1px solid transparent;
        &:hover{
            /* IE 8 */
            -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=80)";
            /* IE 5-7 */
            filter: alpha(opacity=80);
            /* Netscape */
            -moz-opacity: 0.8;
            /* Safari 1.x */
            -khtml-opacity: 0.8;
            /* Good browsers */
            opacity: 0.8;
            border: 1px solid @gray-base;
        }
        div{
            display: table;
            width: 100%;
            height: 100%;
            & > span{
                display: table-cell;
                text-align: center;
                vertical-align: middle;
                color: white
            }
            .title{
                display: block;
                font-family: @font-family-thin;
            }
            .value{
                font-size: 5.8vw;
                line-height: 1em;
                display: block;
                padding-top: 1vw;
                @media(max-width: @screen-xs-max){
                    font-size: 18vw;
                }
                @media (min-width: @screen-md) {
                    font-size: 77px;
                }
            }
            .message{
                font-size: 1vw;
                line-height: 1em;
                display: block;
                padding-top: 5.5vw;
                @media(max-width: @screen-xs-max){
                    font-size: 6vw;
                }
            }
        }
    }
    &.account-btn{
        .account-content{
            bottom:43px;
        }
        .tile-btn{
            position: absolute;
            bottom: 0;
        }
    }

    .tile-badge{
        position: absolute;
        top:0;
        right: 0;
        cursor: help;
        font-size: @font-size-h4;
        border-radius: 0;
        @media (max-width: @screen-sm-max) {
            font-size: 12px;
        }
        @media (max-width: @screen-xs-max) {
            font-size: 5vw;
        }
    }
}


/* CECS-132 billing options improvements */
i.submit-changes-success {
    color: @brand-primary;
