}

.account-nav-links{
    .active{
        &:before{
            display: inline-block;
            font: normal normal normal 14px/1 FontAwesome;
            font-size: inherit;
            text-rendering: auto;
            -webkit-font-smoothing: antialiased;
            -moz-osx-font-smoothing: grayscale;
            transform: translate(0, 0);
            content:"\f105";
            color: @brand-orange;

        }
        a{
            color: @brand-orange;
        }
    }

}

.accountContentPane{

    form{
        float:left;
    }

    .emptyMessage{
        margin-bottom: 10px;
    }
    .data-list{
        dt{
