.items-list{
    .product{
        text-align: center;
        display: block;
        margin-bottom: 15px;
        img{
            margin:0 auto;
            width: 100%;
            margin-top: 20px;
            margin-bottom: 20px;
            @media (max-width: @screen-xs-max) {
                width:100%;
                margin-top:1vh;
                margin-bottom:1vh;
            }
        }
        .name,.price{
            display: block;
        }
        .name{
            //display:inline-block;
            height:2.8em;
            line-height:2.8em;
            //vertical-align: middle;
            .inner{
                display:inline-block;
                vertical-align: middle;
                line-height:1.4em;
            }
        }
        .price{
            color: @brand-dark;
            line-height: 1.4em;
            height: 1.4em;
        }
        .photo_bg{
            width: 100%;
            height: 40vh;
            background-size: contain;
            background-repeat: no-repeat;
            background-position: center;
            border: 0px;
            display: block;
            margin: 0 auto;
            @media(max-width: @screen-xs-max){
                height: 20vh;

            }
        }
    }
}
